ymaps.ready(init);
function init() {
 
    var myMap = new ymaps.Map("YMaps", {
        center: mapcenter,
        zoom: 16,
        // отключить эл. управления
        controls: [],
        // Тип карты
        type: "yandex#map"
    });
    myMap.behaviors.disable('scrollZoom');
    myMap.behaviors.disable('drag');

    // Все виды меток
    // https://tech.yandex.ru/maps/doc/jsapi/2.1/ref/reference/option.presetStorage-docpage/
 

    // Собственное изображение для метки с контентом
    var myPlacemark1 = new ymaps.Placemark(mapcenter, {
            balloonContent: maptext,
            hintContent:    maptext
        }, {
            draggable: false,
            hideIconOnBalloonOpen: false,
            iconLayout: 'default#image',
            // iconImageClipRect: [[0,0], [90, 70]],
            iconImageHref: '/bitrix/templates/rubytech/img/map-logo.png',
            iconImageSize:   [ 90,  70],
            iconImageOffset: [-45, -70]
        })
 
    myMap.geoObjects.add(myPlacemark1);
}